.delivery {
  margin: 0 auto;
  display: flex;
  flex-direction: column;

  &__header {
    content: '';
    display: table;
    width: 100%;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.21;
    padding: 0px 16px 32px 16px;
  }
  @include tablet-only {
    font-size: 44px;
    margin-bottom: 63px;
  }
  @include desktop {
    font-size: 44px;
    margin-bottom: 103px;
  }
}
.line {
  display: table-cell;
  width: 100%;
  padding-left: 14px;
  border: none;
}
.line:after {
  content: '';
  border-top: 1px solid #000000;
  width: 100%;
  display: inline-block;
  vertical-align: middle;
}
.delivery-list {
  margin: 0px 24px 35px 16px;
  display: flex;
  flex-direction: column;

  @include tablet-only {
  }
}

.delivery__item {
  display: flex;
  align-items: center;
  // flex-direction: row;
  // padding-left: 0px;
  // padding-top: 11px;
  // padding-bottom: 9px;
}

