.delivery__step {
  box-sizing: border-box;
  border: 1px solid;
  border-color: $black;
  border-radius: 50%;
  min-width: 40px;
  height: 40px;
  @include tablet-only {
    width: 64px;
    height: 64px;
    // margin-right: 20px;
  }
  @include desktop {
    margin-right: 56px;
  }
  display: flex;
  justify-content: center;
  align-items: center;
  // margin-right: 8px;

  // font-size: 26px;
