  // text-align: center;
  // vertical-align: middle;

  // height: 64px;
  // width: 64px;

  // margin-right: 58px;
  // padding-top: 16px;
  // padding-bottom: 16px;
}

.delivery__number {
  font-style: normal;
  // font-weight: 400;
  font-size: 20px;
  line-height: 1.2;
  @include tablet-only {
    font-size: 26px;
  }
}

.delivery__description {
  padding-left: 20px;
  margin-bottom: 14px;
  // font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  @include desktop {
    font-size: 32px;
  }

  // font-size: 32px;
  // line-height: 1.25;
  // color: $black;
  // vertical-align: middle;
  // margin-left: 56px;
}

.delivery-image {
  width: 100%;
  margin-top: 35px;
  margin-bottom: 76px;
  @include tablet-only {
    margin-top: 56px;
    margin-bottom: 128px;
  }
  @include desktop {
    margin-top: 82px;
    margin-bottom: 128px;
  }
}
